import React from 'react';
import Noty from 'noty';
import 'noty/lib/noty.css';
import Word from './Word';
import Input from './Input';
import Button from './Button';
import Image from './Image';

import Audio from './Audio';

import Video from './Video';
import Shape from './Shape';
import Note from './Note';

import store from '../../../store';
import { changeFocus, replacePage } from '../../../actions/h5Actions';
import './page.less';

/**
 * @param viewing 是否正在浏览
 */
function Page({ page = { elements: [], style: {} }, focusId, viewing = false, template = false }) {
    const handleClick = e => {
        if (!viewing && e.target === e.currentTarget) { // 防止捕获其它onclick事件
            store.dispatch(changeFocus({ ...page }));
        }
        if (template) {
            const n = new Noty({
                text: '页面模板会覆盖编辑区域已有组件，确定继续? ',
                layout: 'center',
                type: 'alert',
                modal: true,
                buttons: [
                    Noty.button('确定', 'noty-confirm', () => {
                        store.dispatch(replacePage(page));
                        n.close();
                    }),

                    Noty.button('取消', 'noty-cancel', () => {
                        n.close();
                    }),
                ],
            }).show();
        }
    };
    return (
        <div style={page.style} onClick={handleClick}>
            {
                page.elements.map(element => {
                    switch (element.name) {
                    case 'WordModal': return <Word key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'InputModal': return <Input key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'ButtonModal': return <Button key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'ImageModal': return <Image key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'AudioModal': return <Audio key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'VideoModal': return <Video key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'ShapeModal': return <Shape key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    case 'NoteModal': return <Note key={element.id} value={element} focusId={focusId} viewing={viewing} />;
                    default: return null;
                    }
                })
            }
        </div>
    );
}

export default Page;



