import React from 'react';
import Rnd from '../../common/rnd/ReactRnd';
import store from '../../../store';
import { changeFocus } from '../../../actions/h5Actions';

export default class Audio extends React.Component {
    onClicked = e => {
        e.stopPropagation();
        store.dispatch(changeFocus(this.props.value));
    };

    disableDrag = e => {
        e.preventDefault();
    };

    render() {
        const value = this.props.value;
        const focusId = this.props.focusId;
        return (
            <Rnd
                onDragStart={this.onClicked}
                className={focusId === value.id ? 'focused' : ''}
                style={value.style}
            >
                <audio src={value.src} />
                <img onMouseDown={this.disableDrag} src={require('../dialog/images/audio_dom.png')} style={{ width: '100%', height: '100%' }} />
            </Rnd>
        );
    }
}




